import Link from "next/link";
import { Input } from "@/components/ui/input";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";

async function getStories(params: { q?: string; tag?: string; page?: number }) {
  const q = new URLSearchParams();
  if (params.q) q.set("q", params.q);
  if (params.tag) q.set("tag", params.tag);
  if (params.page) q.set("page", String(params.page));
  const res = await fetch(`/api/stories?${q.toString()}`, { next: { revalidate: 60 } });
  if (!res.ok) throw new Error("Failed to load stories");
  return res.json() as Promise<{ items: any[] }>;
}

export default async function ExplorePage({
  searchParams,
}: {
  searchParams?: { q?: string; tag?: string; page?: string };
}) {
  const data = await getStories({
    q: searchParams?.q,
    tag: searchParams?.tag,
    page: searchParams?.page ? Number(searchParams.page) : 1,
  });

  return (
    <div className="space-y-6">
      <form className="flex items-center gap-3" action="/explore">
        <Input
          name="q"
          defaultValue={searchParams?.q ?? ""}
          placeholder="Search stories, authors, tags..."
          className="max-w-lg"
        />
      </form>

      <Tabs defaultValue={searchParams?.tag ? "tags" : "trending"} className="space-y-4">
        <TabsList>
          <TabsTrigger value="trending">Trending</TabsTrigger>
          <TabsTrigger value="new">New</TabsTrigger>
          <TabsTrigger value="tags">Tags</TabsTrigger>
        </TabsList>
        <TabsContent value="trending" className="grid gap-3 sm:grid-cols-2 lg:grid-cols-3">
          {data.items.map((s) => (
            <Link key={s._id} href={`/stories/${s._id}`} className="p-3 rounded-md border hover:bg-muted">
              {s.title}
            </Link>
          ))}
        </TabsContent>
        <TabsContent value="new">Freshly published stories.</TabsContent>
        <TabsContent value="tags" className="flex gap-2 flex-wrap">
          {["Fiction", "Poetry", "Kids", "Nature", "Sciâ€‘Fi", "Romance"].map((t) => (
            <Link key={t} href={`/explore?tag=${t}`} className="px-3 py-1 rounded-md border hover:bg-muted text-sm">
              #{t}
            </Link>
          ))}
        </TabsContent>
      </Tabs>
    </div>
  );
}
